export default ()=> {
      return (
        <div className="question" data-index="6" dangerouslySetInnerHTML = {{ __html: `
<h6>6. 输出是什么？</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-let">let</span> c <span class="token operator">=</span> <span class="token punctuation">{</span> greeting<span class="token operator">:</span> <span class="token string">'Hey!'</span> <span class="token punctuation">}</span>
<span class="token keyword keyword-let">let</span> d

d <span class="token operator">=</span> c
c<span class="token punctuation">.</span>greeting <span class="token operator">=</span> <span class="token string">'Hello'</span>
console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>d<span class="token punctuation">.</span>greeting<span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: <code>Hello</code></li>
<li>B: <code>undefined</code></li>
<li>C: <code>ReferenceError</code></li>
<li>D: <code>TypeError</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: A</h4>
<p>在 JavaScript 中，当设置两个对象彼此相等时，它们会通过<em>引用</em>进行交互。</p>
<p>首先，变量 <code>c</code> 的值是一个对象。接下来，我们给 <code>d</code> 分配了一个和 <code>c</code> 对象相同的引用。</p>
<img src="https://i.imgur.com/ko5k0fs.png" width="200">
<p>因此当我们改变其中一个对象时，其实是改变了所有的对象。</p>
</p>
</details>
` }}></div>
      );
    }